<template>
    <div class="people">
        <top-info @time="getTime"></top-info>
        <list
            v-model="loading"
            :finished="finished"
            @load="onLoad">
            <div class="item" v-for="(item, index) of list" :key="index">
                <img class="avatar" :src="item.avatar_url" alt="">
                <p class="text">你的客户{{item.nickname}}浏览你的小程序{{item.allnum}}个页面</p>
            </div>
        </list>
        <div v-if="list.length <= 0 && finished" class="no-data">暂无数据...</div>
    </div>
</template>

<script>
import { List, Icon } from 'vant';
import TopInfo from '@/components/TopInfo/TopInfo';
import { getPeopleList } from '../../server/data-tracking';
import { getItem } from '../../util/util';
export default {
    data () {
       return {
            loading     : false,
            finished    : false,
            list        : [],
            offset      : -20,
            cardId      : 0,
            lastid      : 0,
            startday    : '',
            endday      : ''
       };
    },
    created() {
        if(getItem('cardId')) {
            this.cardId = getItem('cardId');
        }
    },
    methods: {
        onLoad() {
            this._getPeopleList();
        },
        async _getPeopleList() {
            try {
                let res = await getPeopleList({
                    cardid      : this.cardId, 
                    lastid      : this.lastid, 
                    startday    : this.startday, 
                    endday      : this.endday 
                });
                if(res.data.info.length > 0) {
                    // debugger;
                    for(let i=0; i<res.data.info.length; i++){
                        this.list.push(res.data.info[i]);
                    }
                    this.lastid     = res.data.info[res.data.info.length-1].user_id;
                    this.loading    = false;
                    this.finished   = false;
                }else{
                    this.loading    = false;
                    this.finished   = true;
                }
            } catch (error) {
                
            }
        },
        getTime(day) {
            this.loading    = false;
            this.finished   = false;
            this.list       = [];
            this.lastid     = 0;
            this.startday   = day.startday;
            this.endday     = day.endday;
        }
    },
    components: {
        TopInfo,
        List,
        Icon
    }
}
</script>

<style lang='less' scoped>
@import url(./index.less);
</style>


