<template>
    <div class="top-info">
        <p class="text">{{msg}}被查看的行为统计</p>
        <span class="iconfont icon-rili" @click="sendTime"></span>
        <actionsheet v-model="show" :actions="actions" cancel-text="取消" />
    </div>
</template>

<script>
import { List, Icon, Actionsheet } from 'vant';
import { getNowFormatDate, getPreMonth } from '../../common/js/time';
export default {
    data () {
       return {
            show    : false,
            actions : [
                {
                    name: '七天内',
                    callback: this.onClickWeek
                },
                {
                    name: '一个月内',
                    callback: this.onClickMonth
                }
            ],
            msg     : '七天内'
       };
    },
    created() {},
    methods: {
        sendTime() {
            this.show = true;
        },
        // 选择七天的统计
        onClickWeek() {
            this.$emit('time', {
                isMonth     : false,
                startday    : '',
                endday      : ''
            })
            this.msg    = '七天内';
            this.show   = false;
        },
        // 选择一个月的统计
        onClickMonth() {
            let endday    = getNowFormatDate();
            let startday  = getPreMonth(endday);
            this.$emit('time', {
                isMonth: true,
                startday,
                endday
            })
            this.msg    = '一个月内';
            this.show   = false;
        }
    },
    components: {
        Icon,
        Actionsheet
    },
    computed: {},
}
</script>

<style lang='less' scoped>
@import url(./index.less);
</style>


